.searchBar {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 20%;


  .inputCity {
    border: none;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.6);
    opacity: 0.6;
    transition: all ease-in-out 0.3s;
    color: black;
    outline: 1px solid grey;

  }

  &:focus {
    outline: 1px solid #6e6e6e;
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.05);
    transition: all ease-in-out 0.3s;
  }


  #inputCity:hover {
    opacity: 1;
    outline: 1px solid #6e6e6e;
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.05);
    transition: all ease-in-out 0.3s;
  }
}

.clickBtn {
  display: flex;
  flex-direction: column;
  .fetchCity {
    border: none;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    padding: 10px;
    margin: 0 10px;
    font-size: 14px;
    border-radius: 10%;
    background-color: rgba(255, 255, 255, 0.6);
    opacity: 0.6;
    transition: all ease-in-out 0.3s;
    color: black;
    outline: 1px solid grey;

    &:hover {
      opacity: 0.8;
      transform: scale(1.1);
      cursor: pointer;
    }
  }
}
